import React, { Component, PropTypes } from 'react';
import { Router, Route, Link ,hashHistory} from 'react-router';
// import _ from 'underscore';
// import classNames from 'classnames'
import globalData from './data'; //
// start
import './receivables.scss'
// end
import Backbar from '../../module/backbar/backbar';
import Picker from '../../widget/picker/picker';
import Validate from '../../widget/react-validate/react-validate';
import { Modal } from 'antd-mobile';
import Alert from '../../module/alert/alert';


class Receivables extends Component{
    constructor(){
        super();
    }
    componentWillMount(){
        var that=this;
        //
        this.state={
            buttonState:true,
            trueName:'',
            certCode:'',
            ll_banckCode:'',
            form:{
                parson:'',
                bankName:'',
                bankCode:'',
                code:''
            },
            isCode:true,
            codeCount:60,
            data:[],
            getUserInfo : ""

        }
        //
        // Utils.requestData({
        //     url: config.api + 'qjlianlian/front/v1/account/getMyBankCard',
        //     method: 'post',
        //     data: {},
        //     callback: function(data){
        //         if(data.resultCode == 0){
                     

        //         }
        //     }

        // });
        
        if(Utils.Url.parseUrl(location.href).params.isWap=="true"){  //app
            
            componentStore.update(that,{
                qpToken : Utils.Url.parseUrl(location.href).params.qpToken
            });

        }else{

            // 
            componentStore.update(that,{
                qpToken : Utils.Storage.get('user').token
            });

            Utils.tokenExpireJumpToLogin(function () {});

        }
            
        
        Utils.requestData2({
            requestArr:[{//
                url: config.api + 'qjlianlian/front/v1/account/getMyBankCard',
                method: 'post',
                qpToken: that.state.qpToken,
                data: {},
            },
                {
                    url: config.api + 'qjlianlian/front/v1/user/getUserInfo',
                    method: 'post',
                    qpToken: that.state.qpToken,
                    data: {},
                },
            ],
            callback: function(dataArr){
                var bankDetail = dataArr[0];
                if(bankDetail.resultCode == 0){
                    // componentStore.update(that,{
                    //     ll_banckCode:bankDetail.data.bankCardReal
                    // })
                }
                var userInfo = dataArr[1];
                if(userInfo.resultCode == 0){
                    componentStore.update(that,{
                        trueName:userInfo.data.trueName,
                        certCode:userInfo.data.certCode,
                        getUserInfo:userInfo.data
                    })
                    if(userInfo.data.isTrueMan==1){//
                        componentStore.update(that,{
                            form:{
                                // phone:userInfo.data.phone,//
                                // bankCode:bankDetail.data.bankCardReal,//
                            }
                        })
                    }
                }
            }
        })
    



    }

    componentDidMount(){
        //  start
        componentStore.set(this);
        //  end
    };
    componentWillUnmount(){
        //  start
        componentStore.clear(this);
        //  end
    };


    //

    onYes(data){
        let dataL=this.state.data;
        for(var i=0;i<dataL.length;i++){
            if(dataL[i].id == data.idArr[0]){
                 this.state.form.bankName = dataL[i].label;
                componentStore.update(this);
            }
        }
    }
    onChange(data){

     }
    handleRemove(i) {
        var newItems = this.state.items;
        newItems.splice(i, 1);
        this.setState({items: newItems});
    }
    //
    getCode(){
        //alert('1');
        let that = this;
        that.refs.validator.validate({
            eleNames: ['phone'],
            callback: function(res){
                if(res){
                    that.code();
                }
            }
        })
    }
    //
    code(){
        let that = this;
            componentStore.update(that,{isCode:false});
            let count = 60;
        let time=setInterval(function(){
                count--;
                componentStore.update(that,{codeCount:count});
            if(count<=0){
                componentStore.update(that,{isCode:true});
                componentStore.update(that,{codeCount:60});
                clearInterval(time);
            }
        },1000)
    }
    render(){
        var that = this;
        let codeText = that.state.isCode? <i onClick={this.getCode.bind(this)}></i> : <i style={{color:'#ccc'}}>{that.state.codeCount+''}</i>
        return (
            <div className="receivables">
                <Backbar $id="receivables" title="" >
                    <div className="bankReceivables">
                        <Validate $id="receivablesBank" ref="validator"
                            onError={(obj)=>{
                                //console.error(''+rule+'')
                                Modal.alert('',obj.errorMsg, [
                                    { text: '', onPress: () => console.log('cancel') },
                                    { text: '', onPress: () => console.log('ok'), style: { fontWeight: 'bold' } },
                                ])
                            }}>
                            {/*<div className="bankBingdImg">
                                <img src={require('../../../image/img/bankBingdLog.png')} alt=""/>
                            </div>*/}
                            {/**/}
                                <div className="inputItem flex-father">
                                    <div className="label"></div>
                                    <div className="flex-child1">
                                        <input className="inputText" type=" " value={that.state.userName} placeholder=""
                                                onChange={(obj)=>{
                                                    that.state.userName = obj.target.value;
                                                    componentStore.update(that,that.state)
                                                }}
                                                data-validName="userName"
                                                data-validRules={[
                                                    {name: 'require'}
                                            ]}
                                                data-validMsgPrefix={''}
                                        />
                                    </div>
                                </div>
                            {/**/}
                                <div className="inputItem flex-father">
                                    <div className="label"></div>
                                    <div className="flex-child1">
                                        <input className="inputText" type="text" value={that.state.certCode} placeholder=""
                                                onChange={(obj)=>{
                                                    that.state.certCode = obj.target.value;
                                                    componentStore.update(that,that.state)
                                                }}
                                                data-validName="certCode"
                                                data-validRules={[
                                                    {name: 'require'},
                                                    {name:'isCardNo'}
                                            ]}
                                                data-validMsgPrefix={''}
                                        /> 
                                    </div>
                                </div>
                            
                            {/**/}
                               <div className="inputItem flex-father">
                                    <div className="label"></div>
                                    <div className="flex-child1">
                                        <input className="inputText" type="text" value={that.state.form.bankCode} placeholder=""
                                            onChange={(obj)=>{
                                                that.state.form.bankCode = obj.target.value;
                                                componentStore.update(that,that.state)
                                            }}
                                            onBlur={()=>{
                                                Utils.requestData2({
                                                        requestArr:[{//
                                                            url: config.api + 'qjlianlian/front/v1/lianlianInvest/getBankCardBin',
                                                            method: 'post',
                                                            qpToken: that.state.qpToken,
                                                            data: {
                                                                'bankCard':that.state.form.bankCode
                                                            },
                                                        }],
                                                        callback: function(dataArr){
                                                            var bankDetail = dataArr[0];
                                                            if(bankDetail.resultCode == 0){
                                                                console.log('',bankDetail.data.bankName)
                                                                that.state.form.bankName = bankDetail.data.bankName
                                                                componentStore.update(that,that.state)
                                                            }
                                                        }
                                                })
                                            }}
                                            data-validName="bankCode"
                                            data-validRules={[
                                                {name: 'require'}
                                        ]}
                                            data-validMsgPrefix={''}
                                        />
                                    </div>
                                </div>
                            {/**/}
                               <div className="inputItem flex-father">
                                    <div className="label"></div>
                                    <div className="flex-child1">
                                        {/*<input className="inputText code" type="text" value={that.state.form.bankName1} placeholder=""
                                            onChange={(obj)=>{
                                               that.state.form.bankName = obj.target.value;
                                               componentStore.update(that,that.state)
                                           }}
                                            data-validName="bankName1"
                                            data-validRules={[
                                                {name: 'require'},
                                            ]}
                                            data-validMsgPrefix={''}
                                        />*/}
                                        {/*<span className="getCode">{codeText}</span>*/}
                                        {that.state.form.bankName}
                                    </div>
                                </div>
                            {/**/}
                                {/*<div className="inputItem flex-father">
                                    <div className="label"></div>
                                    <div className="flex-child1">
                                        <input className="inputText inputCodeText" type="number" value={that.state.form.code} placeholder=""
                                            onChange={(obj)=>{
                                               that.state.form.code = obj.target.value;
                                               componentStore.update(that,that.state)
                                           }}
                                            data-validName="code"
                                            data-validRules={[{
                                               name: 'require'
                                           }]}
                                            data-validMsgPrefix={''}
                                        />
                                        <span className="codeLast"></span>
                                    </div>
                                </div>*/}
                                {/*<div className="prompt"></div>*/}
                                <p className="bankNotes">&lt; &gt;-&lt;&gt;</p>
                            {/**/}
                                <div className="inputItem noBorder bnt-red mt30" style={{display:that.state.buttonState?'block':'none'}} onClick={()=>{
                                    Utils.eventHanlder(function(){
                                        that.refs.validator.validate({
                                            callback: function(res){
                                                if(res){
                                                    componentStore.update(that,{
                                                            buttonState:false
                                                        })
                                                        console.log('')
                                                    Utils.requestData({
                                                        url: config.api + 'qjlianlian/front/v1/lianlianInvest/ajaxUser/addMemberBankCard',
                                                        method: 'post',
                                                        qpToken: that.state.qpToken,
                                                        data:{
                                                            'trueName':that.state.userName,
                                                            'certCode':that.state.certCode,
                                                            "bankCard": that.state.form.bankCode,
                                                            "bankName": that.state.form.bankName
                                                        },
                                                        callback: function(data){
                                                            if(data.resultCode==0){
                                                                console.log('',data.data);
                                                                Utils.backRoute();
                                                                // Alert({
                                                                //     type:'standard',
                                                                //     title:'',
                                                                //     desc:'',
                                                                //     button:[
                                                                //         {
                                                                //             text:'',
                                                                //             handle:()=>{
                                                                //                 Utils.backRoute();
                                                                //             }
                                                                //         }
                                                                //     ]

                                                                // });
                                                            }
                                                        },
                                                        errorAlert:function(){
                                                            console.log('');
                                                            componentStore.update(that,{
                                                                buttonState:true
                                                            })
                                                        }
                                                    });
                                                }
                                            }
                                        });

                                    })
                                }}>
                                    
                                </div>
                                <div className="inputItem noBorder bnt-red mt30" style={{display:that.state.buttonState?'none':'block'}} onClick={()=>{console.log('')}}>
                                    
                                </div>
                                {/*<p className="look-bankList" onClick={()=>{
                                    //Utils.switchRoute('')
                                }}>
                                    
                                </p>*/}
                        </Validate>
                       {/* <div className="btn_bankList"><a href="/bankList"></a></div>*/}
                    </div>
                </Backbar>
            </div>
        )
    }

}

export default Receivables;



